import React, { Component } from 'react';
import { Button, Form } from 'react-bootstrap';
import { motion } from "framer-motion"
import * as nearAPI from "near-api-js";

import classes from './wallet.module.css';
import globalClasses from '../../constants/globalStyles.module.css';

const { connect, keyStores, WalletConnection } = nearAPI;


export default class Wallet extends Component {

    constructor(props) {
        super(props);
        this.state = {
            barValue:0
        };
        this.config = {
            networkId: "testnet",
            keyStore: new keyStores.BrowserLocalStorageKeyStore(),
            nodeUrl: "https://rpc.testnet.near.org",
            walletUrl: "https://wallet.testnet.near.org",
            helperUrl: "https://helper.testnet.near.org",
            explorerUrl: "https://explorer.testnet.near.org",
        };
        this.wallet = "";
    }

    componentDidMount() {

        connect(this.config)
        .then(res => {

            this.wallet = new WalletConnection(res);

            if(this.wallet.isSignedIn()) {
                console.log(this.wallet.getAccountId(),'this.walletaccount');
            }
        })
        .catch(err => {
           console.log(err);
        });

    }

    signIn = () => {
        this.wallet.requestSignIn({
            successUrl: "http://localhost:3000/connectWallet",
            failureUrl: "http://localhost:3000/404" 
        });
    };
    
    render() {

        return (
            <div>
                <Button onClick={this.signIn} className={globalClasses.primaryBtn}>
                    Connect Wallet
                </Button>
            </div>
        )
    }
}
